}
.containerStyleExist {
  padding: 12px 10px;
  margin: 10px;
  border-color: lightseagreen;
  border-width: 1px;
  border-style: solid;
  border-radius: 5px;
  background-color: rgb(70, 255, 245);
}
.crossIconStyle {
  background: transparent;
  height: 20px;
  width: 20px;
  font-size: 10px;
  border-radius: 15px;
  border-style: solid;
  border-width: 1.5px;
